/*  Styles for the language switcher         */
/*  navigation, including padding, font size,*/
/*  active states, and hover effects.        */
/*********************************************/

.theme-mainnav-variation-left {
  #theme-page-language {
    li {
      &:first-child {
        padding-left: 0;
      }
    }
  }
}

#theme-page-language {
  list-style: none;
  @include media-breakpoint-down(lg) {
    padding: 1rem;
    padding-right: 0;
  }

  li {
    padding: 5px;
    font-size: 11px;
    font-weight: 700;

    &:first-child {
      @include media-breakpoint-down(lg) {
        padding-left: 0;
      }
    }

    a {
      color: var(--bs-body-color);
      text-decoration: none;

      @include media-breakpoint-down(lg) {
        color: rgba(var(--bs-white), 0.8);
      }

      &:hover {
        color: var(--bs-primary);
      }
    }

    &.active a {
      color: var(--bs-primary);

      @include media-breakpoint-down(lg) {
        color: #fff;
      }
    }
  }
}
