/*  Contains styling for the mobile "Back to */
/*  Top" button, including its fixed         */
/*  positioning, opacity transitions, and    */
/*  visibility handling based on scroll.     */
/*********************************************/

a#theme-to-top {
	position: fixed;
	bottom: 20px;
	right: 25px;
	background-color: var(--bs-primary);
	width: 40px;
	height: 40px;
	box-shadow: 0 0 4px rgba(0,0,0,.3);
	opacity: 0;
	z-index: 100;
	transition: opacity 0.5s;
	pointer-events: none;
	
	&.theme-top-visible {
		opacity: 1;
		pointer-events: auto;
	}
}